* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

#container {
  background-color: #8fbc8b;
  width: 100%;
  margin: auto;
}

header {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 10px;
  display: flex;
}

h1 {
  text-align: center;
  margin: 10px;
  padding: 10px;
  font-size: 30px;
}

h1:hover {
  cursor: pointer;
  text-decoration: underline;
}

form {
  text-align: center;
  margin-top: 10px;
  margin-bottom: 10px;
}

form input, form button {
  padding: 5px;
}

.icons a {
  color: #000;
  margin: 5px;
  font-size: 20px;
}

#productCard {
  background-color: #8fbc8b;
  flex-wrap: wrap;
  gap: 20px;
  display: flex;
}

.product-card {
  width: calc(33.33% -20px);
  background-color: #fff;
  border: 1px solid #000;
  border-radius: 5px;
  flex: 1 0 25%;
  margin-bottom: 10px;
  padding: 10px;
}

.product-card p {
  margin-bottom: 5px;
}

.imgContainer {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 200px;
  display: flex;
}

.imgContainer img {
  object-fit: contain;
  align-items: center;
  max-width: 100%;
  max-height: 200px;
}

.product-card button {
  margin: 10px auto 0;
  padding: 5px;
  display: block;
}

@media (width >= 1024px) {
  #productCard .product-card {
    flex: 1 0 20%;
    width: calc(20% - 20px);
  }
}
